import {
  Container,
  DetailsContainer,
  Image,
  InfoContainer,
  Title,
  TitleSpan,
  Text,
  MoreNavigate,
  MoreInfo,
  More,
  MoreList,
  MoreItem,
  LinkTo,
} from './MovieDetails.styled';
import PropTypes from 'prop-types';
import { Outlet, useLocation } from 'react-router-dom';

export default function MovieDetails({ movieInfo }) {
  const {
    originalTitle,
    title,
    genres,
    overview,
    poster,
    releaseDate,
    voteAverage,
  } = movieInfo;

  const genresArray = genres.map(genre => genre.name).join(', ');

  const location = useLocation();

  return (
    <Container>
      <DetailsContainer>
        <Image
          src={
            poster
              ? `https://image.tmdb.org/t/p/w500${poster}`
              : 'https://image.tmdb.org/t/p/w500/4XlgNK3hLcw5yemwZYMkBvvyf1A.jpg'
          }
          alt={title}
        />
        <InfoContainer>
          <Title>
            {originalTitle ? originalTitle : '   '}
          </Title>
          <Text>
            <TitleSpan> : </TitleSpan> {title ? title : ' '}
          </Text>
          <Text>
            <TitleSpan>: </TitleSpan>{' '}
            {voteAverage ? voteAverage : ''}
          </Text>
          <Text>
            <TitleSpan> : </TitleSpan>{' '}
            {releaseDate ? releaseDate : '  '}
          </Text>
          <Text>
            <TitleSpan>: </TitleSpan>{' '}
            {genresArray ? genresArray : ''}
          </Text>
          <Text>
            <TitleSpan>: </TitleSpan>
            {overview
              ? overview
              : '    ,           .'}
          </Text>
        </InfoContainer>
      </DetailsContainer>
      <MoreInfo>
        <More> </More>
      </MoreInfo>
      <MoreNavigate>
        <MoreList>
          <MoreItem>
            <LinkTo to="credits" state={{ from: location }}>
               
            </LinkTo>
          </MoreItem>
          <MoreItem>|</MoreItem>
          <MoreItem>
            <LinkTo to="reviews" state={{ from: location }}>
              
            </LinkTo>
          </MoreItem>
        </MoreList>
      </MoreNavigate>
      <Outlet />
    </Container>
  );
}

MovieDetails.propTypes = {
  movieInfo: PropTypes.shape({
    originalTitle: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    genres: PropTypes.array.isRequired,
    overview: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    releaseDate: PropTypes.string.isRequired,
    voteAverage: PropTypes.number.isRequired,
  }).isRequired,
};
